@keyframes rocket {
  from {
    right: -200px;
    top: 60%;
  }

  to {
    right: 50%;
    top: -200px;
  }
}

.rocket {
  -webkit-animation: rocket 2.5s ease-in-out;
  animation: rocket 2.5s ease-in-out;
}

